<template>
    <div>
        <!-- Start of Navigation -->
        <Menu></Menu>
        <!-- End of Navigation -->
        <v-content class="ma-3">
            <div class="pa-4">
                <div class="title">Backup</div>
                <v-btn large :href="$controllerBase + '/kvstore'" target="_blank">Download Key/Value Store</v-btn><br />
            </div>
            <v-divider></v-divider>

            <div class="pa-4">
                <div class="title">Restore</div>
                <v-file-input show-size label="Select JSON file for import" @change="loadTextJSONFile"></v-file-input>
                <v-btn large @click="uploadJSON">Upload Key/Value Store</v-btn><br />
            </div>
            <v-snackbar
                    v-model="snackbar"
                    :timeout="snackbartimeout"
                    :color="snackbarcolor"
            >
                {{ snackbartext }}
                <v-btn
                        text
                        @click="snackbar = false"
                >
                    Close
                </v-btn>
            </v-snackbar>
        </v-content>
    </div>
</template>

<script>
    import axios from 'axios';
    import BRemoteMenu from './BRemoteMenu'

    export default {
        name: "BRemoteKVStore",
        components: {
            Menu: BRemoteMenu,
        },
        props: ['machine'],
        data: () => ({
            drawer: true,
            jsonstr: '',
            snackbartext: '',
            snackbarcolor: 'blue',
            snackbar: false,
            snackbartimeout: 2000,
        }),
        methods: {
            uploadJSON() {
                let me = this
                if( me.jsonstr.length <= 2 ) {
                    me.snackbartext = "no json data loaded"
                    me.snackbarcolor = 'error'
                    me.snackbartimeout = 4000
                    me.snackbar = true
                    return
                }
                axios
                    .post(me.$controllerBase + '/kvstore', me.jsonstr)
                    .then(response => {
                        me.snackbartext = "json uploaded to key/value store" + JSON.stringify(response.data)
                        me.snackbarcolor = 'blue'
                        me.snackbartimeout = 2000
                        me.snackbar = true
                    }).catch((error) => {
                    me.snackbartext = "error uploading json to key/value store: " + error
                    me.snackbarcolor = 'error'
                    me.snackbartimeout = 4000
                    me.snackbar = true
                })

            },
            loadTextJSONFile(file) {
                let me = this
                const reader = new FileReader();
                reader.onload = (e => {
                    me.jsonstr = e.target.result
                    me.snackbartext = "data loaded into memory"
                    me.snackbarcolor = 'blue'
                    me.snackbartimeout = 2000
                    me.snackbar = true
                });
                reader.readAsText(file);
            }
        },
    }
</script>
