<template>
    <nav>
        <!-- <span>RemoteScreen</span>
        <span class="font-weight-light">Controller</span> -->
        <v-app-bar app>
            <v-app-bar-nav-icon
                    @click.stop="drawer = !drawer"
                    class=""
            ></v-app-bar-nav-icon>
            <v-toolbar-title class="headline text-uppercase">
                <span>RemoteScreen</span>
                <span class="font-weight-light">Controller</span>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <span class="font-weight-light">info-age GmbH Basel</span>
        </v-app-bar>
        <!-- End of app toolbar -->
        <!-- Start of mobile side menu -->
        <v-navigation-drawer app v-model="drawer" left>
            <!-- Menu title -->
            <v-toolbar flat>
                <v-list>
                    <v-list-item>
                        <v-list-item-title class="title">Menu</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-toolbar>
            <v-divider></v-divider>
            <!-- Menu Links -->
            <v-list>
                <v-list-item exact to="/overview">
                    <v-list-item-action>
                        <v-icon>{{ machinesIcon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>Overview</v-list-item-content>
                </v-list-item>
                <v-list-item exact to="/kvstore">
                    <v-list-item-action>
                        <v-icon>{{ databaseIcon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>Key/Value Store</v-list-item-content>
                </v-list-item>
                <!-- Submenu screens -->
                <v-list-group value="true">
                    <template v-slot:activator>
                        <v-list-item-title>Screens</v-list-item-title>
                    </template>

                    <v-list-item
                            v-for="client in orderedClients"
                            v-bind:key="client.InstanceName"
                            @click="/*selectMachine(client.InstanceName) */"
                            exact
                            :to="'/screen/'+client.InstanceName"
                    >
                        <v-list-item-action>
                            <v-icon>{{ machineIcon }}</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>{{ client.InstanceName }}</v-list-item-content>
                    </v-list-item>
                </v-list-group>
                <!-- END Submenu screens -->
            </v-list>
        </v-navigation-drawer>
        <!-- End of mobile side menu -->
    </nav>
</template>

<script>
    import axios from 'axios'
    import _ from 'lodash'
    import { mdiMonitor } from '@mdi/js'
    import { mdiMonitorMultiple } from '@mdi/js'
    import { mdiDatabase } from '@mdi/js'

    export default {
        name: "BRemoteNavDrawer",
        props: [],
        data: () => ({
            drawer: true,
            machineIcon: mdiMonitor,
            machinesIcon: mdiMonitorMultiple,
            databaseIcon: mdiDatabase,
            clients: [],
        }),
        computed: {
            orderedClients: function () {
                return _.orderBy(this.clients, 'InstanceName')
            }
        },
        methods: {
            selectMachine: function (name) {
                this.$parent.selectMachine(name)
            }
        },
        mounted () {
            let me = this
            axios
                .get(this.$controllerBase + '/client')
                .then(response => (me.clients = response.data))
        },
    };
</script>
