import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import VueGlobalVar from 'vue-global-var'
import Screens from "./components/BRemoteScreens";
import KVStore from "./components/BRemoteKVStore";
import Overview from "./components/BRemoteOverview";

Vue.config.productionTip = false

Vue.use(VueRouter)

Vue.use(VueGlobalVar, {
  globals: {
    $controllerBase: process.env.NODE_ENV === 'production' ? '' : 'https://127.0.0.1:8444'
  }
})

const routes = [
  {
    path: '/',
    component: Screens,
    props: false,
  },
  {
    path: '/screen/:machine',
    component: Screens,
    props: true,
  },
  {
    path: "/kvstore",
    component: KVStore,
  },
  {
    path: "/overview",
    component: Overview,
  },
]

const router = new VueRouter({
  routes: routes // short for `routes: routes`
})
new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app')
