<template>
    <div>
        <!-- Start of Navigation -->
        <Menu></Menu>
        <!-- End of Navigation -->
        <v-content class="ma-3">
            <v-container fluid>
                <v-row>
                    <v-col cols="12">
                        <v-row
                                align="center"
                                justify="start"
                                class="grey lighten-5"
                        >
                        <v-card
                                v-for="client in orderedClients"
                                v-bind:key="client.InstanceName"
                                class="ma-1"
                                width="300"
                        >
                            <v-img
                                    class="white--text align-end"
                                    height="200px"
                                    v-bind:src="$controllerBase + '/' + client.InstanceName + '/screenshot/thumb'"
                            >

                            </v-img>
                            <v-card-title>{{ client.InstanceName }}</v-card-title>
                            <v-card-subtitle class="pb-0">{{ client.Status }}</v-card-subtitle>

                            <!-- <v-card-text class="text--primary">
                            </v-card-text>
                            -->

                            <v-card-actions>
                                <v-btn
                                        color="orange"
                                        text
                                        :to="'/screen/'+client.InstanceName"
                                >
                                    Detail
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                        </v-row>
                    </v-col>
                </v-row>
            </v-container>
        </v-content>
    </div>
</template>

<script>
    import axios from 'axios'
    import _ from 'lodash'

    import BRemoteMenu from './BRemoteMenu'

    export default {
        name: "BRemoteOverview",
        components: {
            Menu: BRemoteMenu,
        },
        props: ['machine'],
        data: () => ({
            drawer: true,
            clients: [],
        }),
        methods: {
            flipDrawer: function () {
                this.drawer = !this.drawer
            },
            loadClients: function () {
                let me = this
                axios
                    .get(this.$controllerBase + '/client?withstatus')
                    .then(response => (me.clients = response.data))
            },
        },
        mounted () {
            this.loadClients()
        },
        computed: {
            orderedClients: function () {
                return _.orderBy(this.clients, 'InstanceName')
            }
        }
    };
</script>
